import React, { useEffect } from "react";
import { useTranslation } from "react-i18next";
import { Helmet } from "react-helmet";

const ThankYou = () => {
  const { t } = useTranslation();

  // إعادة التوجيه إلى الصفحة الرئيسية بعد مرور 5 ثوانٍ
  useEffect(() => {
    const timer = setTimeout(() => {
      window.location.href = "/"; // إعادة توجيه إلى الصفحة الرئيسية بعد 5 ثوانٍ
    }, 8000);

    return () => clearTimeout(timer); // تنظيف المؤقت عند التدمير
  }, []);

  return (
    <div>
      <Helmet>
        <meta name="description" content={t("thankYou.description")} />
        <meta property="og:title" content={t("thankYou.heading")} />
        <meta property="og:description" content={t("thankYou.description")} />
        <meta property="og:url" content="https://yourwebsite.com/thank-you" />
        <meta property="og:type" content="website" />
        <meta name="twitter:card" content="summary_large_image" />
        <meta name="twitter:title" content={t("thankYou.heading")} />
        <title>{`${t("thankYou.heading")} | G. GROUP S.R.L.`}</title>
      </Helmet>

      <div className="container mt-5">
        <h2 className="text-center mb-4">{t("thankYou.heading")}</h2>
        <div className="alert alert-success text-center">
          <h4>{t("thankYou.message")}</h4>
          <p>{t("thankYou.subMessage")}</p>
          <p>{t("thankYou.redirectMessage")}</p>
        </div>
      </div>
    </div>
  );
};

export default ThankYou;
